.checked{
  border: 5px solid #e29d08f3;
}

input[type="checkbox"][id^="checkbox"] {
  display: none;
}

label {
  border: 13px solid #fff;
  display: block;
  position: relative;
  margin: 10px;
  cursor: pointer;
}

label:before {
  background-color: white;
  color: white;
  content: " ";
  display: block;
  border-radius: 50%;
  border: 1px solid grey;
  position: absolute;
  top: -5px;
  left: -5px;
  width: 25px;
  height: 25px;
  text-align: center;
  line-height: 28px;
  transition-duration: 0.4s;
  transform: scale(0);
}

label img {
    transition-duration: 0.2s;
    transform-origin: 50% 50%;
    height: 67px;
    margin-bottom: -5px;
    margin-left: -5px;
    margin-top: -2px;
    border: 3px solid #e29d08f3;
}

:checked + label {
  border-color: #e29d08f3;
}

:checked + label:before {
  content: "✓";
  background-color: #e29d08f3;
  transform: scale(1);
}

@media screen and (min-width: 1025px)
{
  .contenedor-Ingrediente {
    width: 23%;
    margin: 5px;
    height: 40vh;
    background-position: center;
  }

  label img {
    height: 120px;
  }
  label {
    margin: 10px;
  }
  :checked + label {
    width: 73%;
  }


}
